import React, { useContext, useEffect, useRef, useState } from 'react'
import { useHistory } from 'react-router';
import { CircularProgress } from "@mui/material";

import noteContext from '../context/noteContext'
import AddNote from './AddNote';
import NoteItem from './NoteItem';
const Notes = (props) => {
    const context = useContext(noteContext);
    let history = useHistory();
    const [isLoading, setIsLoading] = useState(false)
    const { notes, getNotes, editNote } = context;
    useEffect(() => {
         setIsLoading(true)
         if(localStorage.getItem('token')){
            getNotes()
             setIsLoading(false)
         }
     else{
             history.push("/login")
         }
     }, [])

    const ref = useRef(null)
    const refClose = useRef(null)
    const [note, setNote] = useState({id: "",etitle: "", edescription:"", etag:"default"})
    
    const updateNote = (currentNote) => {
        ref.current.click();
        setNote({id: currentNote._id,  etitle: currentNote.title, edescription: currentNote.description, etag: currentNote.tag})
    }

    
    const handleClick =(e)=>{
        console.log("updating note", note)
        editNote(note.id, note.etitle, note.edescription, note.etag)
        refClose.current.click();
        props.showAlert("Updated successfully","success")
    }
    const onchange = (e) =>{
        setNote({...note, [e.target.name]: e.target.value})
    }
    if(!localStorage.getItem("token")){
        return(
            <>
                <h1>First Login / Sign Up to add or see your Notes</h1>
            </>
        )
    } else {
        return(
            <>
            <AddNote showAlert={props.showAlert} />
            <button ref={ref} type="button" className="btn btn-primary d-none" data-bs-toggle="modal" data-bs-target="#exampleModal">
        
            </button>
            <div className="modal fade" id="exampleModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div className="modal-dialog">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="exampleModalLabel">Edit note</h5>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div className="modal-body">
                            <form>
                                <div className="mb-3">
                                    <label htmlFor="title" className="form-label">Title</label>
                                    <input type="text" className="form-control" id="etitle" name="etitle" 
                                    value={note.etitle} aria-describedby="emailHelp" onChange={onchange} />
                                </div>
                                <div className="mb-3">
                                    <label htmlFor="desc" className="form-label">Description</label>
                                    <input type="text" className="form-control" id="edescription" name="edescription" value={note.edescription} onChange={onchange} />
                                </div>
                                <div className="mb-3">
                                    <label htmlFor="desc" className="form-label">Tag (Optional)</label>
                                    <input type="text" className="form-control" id="etag" value={note.etag} name="etag" onChange={onchange} />
                                </div>
                            </form>
                        </div>
                        <div className="modal-footer">
                            <button ref={refClose} type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                            <button disabled={note.etitle.length<5 || note.edescription.length<5} onClick={handleClick} type="button" className="btn btn-primary">Update note</button>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row my-3">
                <h1>Your notes</h1>
                
                <div className="container mx-2">
                {isLoading ? <CircularProgress/> : notes.length===0 && 'No notes to display'}
                </div>
                {notes.map((note) => {
                    return <NoteItem key={note._id} updateNote={updateNote} showAlert={props.showAlert} note={note} />
                })}
            </div>
            </>
            )
    }
    
}

export default Notes;
