import React,{useState} from 'react'

export const About = () => {
    const user = localStorage.getItem("token")
    const [name, setName] = useState("")
    const getUserInfo = async () => {
        const res = await fetch("https://inotebookhook-backend.herokuapp.com/api/auth/getuser/", {
            method: "POST",
            headers: {
                "auth-token": localStorage.getItem('token')
            }
        })
        const json = await res.json()
        setName(json.name)
    }

    getUserInfo();

    return (
        <div className="container my-3">
            <div className="jumbotron">
                <h1 className="display-4">Hello, {name}!</h1>
                <h1 className="display-4">•About the application</h1>
                <p>This app was made in a JAVASCRIPT Framework called React.</p>
                <p className="lead"></p>
                <hr className="my-4"/>
                <h3 class ="display-4">•Where this application stores your data</h3>
                <p>This application stores your data in a databse in MongoDB .So no other can see your data or notes . So dont worry your data is safe.</p>
                <p class ="lead"></p>
                <hr class ="my-4"/>
                <h1 class ="display-4">•Who made it ?</h1>
                <p>I am Priyam Shrivastava.I am a student and I am from India.I made this website</p>
                </div>
                <a class ="btn btn-primary btn-sm" target="_blank" href="https://www.youtube.com/c/CodeWithHarry" role="button">Learn coding here</a>
            </div>

            )
}

            export default About;
