import React , {useContext, useState}from 'react'
import noteContext from '../context/noteContext';

const AddNote = (props) => {
    const context = useContext(noteContext);
    const {addNote} = context;

    const [note, setNote] = useState({title: "", description:"", tag:""})
    
    const handleClick =(e)=>{
        e.preventDefault();
        addNote(note.title, note.description, note.tag);
        setNote({title: "", description:"", tag:""})
        props.showAlert("Note Added successfully","success")
    }
    const onchange = (e) =>{
        setNote({...note, [e.target.name]: e.target.value})
    }
    return (
        <div className="container my-3">
            <h1>Add a note</h1>
            <form>
                <div className="mb-3">
                    <label htmlFor="title" className="form-label">Title</label>
                    <input type="text" className="form-control" id="title" name="title" aria-describedby="emailHelp" onChange={onchange} placeholder="Title must be atleast 5 characters" value={note.title}/>
                </div>
                <div className="mb-3">
                    <label htmlFor="desc" className="form-label">Description</label>
                    <input type="text" className="form-control" id="description" name="description" onChange={onchange} placeholder="Description must be atleast 5 characters" value={note.description}/>
                </div>
                <div className="mb-3">
                    <label htmlFor="desc" className="form-label">Tag</label>
                    <input type="text" className="form-control" id="tag" name="tag" onChange={onchange} value={note.tag}/>
                </div>
                <button disabled={note.title.length<5 || note.description.length<5 || note.tag<2} type="submit" className="btn btn-primary" onClick={handleClick}>Add Note</button>
            </form>
        </div>
    )
}

export default AddNote
