import React, {useState} from 'react'
import { useHistory } from 'react-router-dom'   

const Signup = (props) => {
    const [credentials, setCredential] = useState({name:"", email:"", password:"", cpassword:""})
    let history = useHistory();
    const handleSubmit = async (e) => {
        e.preventDefault()
        const {name, password, email} = credentials;
        const response = await fetch("https://inotebookhook-backend.herokuapp.com/api/auth/createuser", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({name, email, password})
        });
        const json = await response.json()
        console.log(json)
        if(json.success){
            localStorage.setItem('token', json.authtoken);
            localStorage.setItem('user', json.user)
            props.showAlert("Account created successfully", "success")
            history.push("/")
        }
        else{
            props.showAlert("Invalid Credentials","danger")
        }
    }

    const onchange = (e) =>{
        setCredential({...credentials, [e.target.name]: e.target.value})
    }
    return (
        <div className="container">
            <form onSubmit={handleSubmit}>
                <div className="mb-3">
                    <label htmlFor="name" className="form-label">Name</label>
                    <input type="text" className="form-control" id="name" 
                    name="name" aria-describedby="emailHelp" onChange={onchange} />
                </div>
                <div className="mb-3">
                    <label htmlFor="exampleInputEmail1" className="form-label">Email address</label>
                    <input type="email" className="form-control" id="exampleInputEmail1" 
                    name="email" aria-describedby="emailHelp"onChange={onchange} />
                </div>
                <div className="mb-3">
                    <label htmlFor="password" className="form-label">Password</label>
                    <input type="password" className="form-control" id="password" name="password"onChange={onchange} minLength={5} required />
                </div>
                <div className="mb-3">
                    <label htmlFor="cpassword" className="form-label">Comfirm  Password</label>
                    <input type="password" className="form-control" id="cpassword" name="cpassword" onChange={onchange} minLength={5} required/>
                </div>
                <button type="submit" className="btn btn-primary">Submit</button>
            </form>
        </div>
    )
}

export default Signup
