import moment from 'moment';
import React ,{useContext} from 'react'
import noteContext from '../context/noteContext';

const NoteItem = (props) => {
    const context = useContext(noteContext);
    const { deleteNote } = context;
    const { note, updateNote } = props;
    return (
        <div className="col-md-4">
            <div className="card my-3">
                <div className="card-body">
                    <h5 className="card-title">{note.title}</h5>
                    <p className="card-text">{note.description}</p>
                    <i className="fas fa-trash-alt mx-2" onClick={()=>{deleteNote(note._id);props.showAlert("Deleted succesfully","success")}}></i>
                    <i className="fas fa-edit mx-2" onClick={()=>{updateNote(note)}}></i>
                    <p className="card-text">#{note.tag}</p>
                    {/* <p className="card-text">Made {moment(note.Date).fromNow()}</p> */}
                    <p className="card-text">Created on - {moment(note.Date).format('MMMM DD, YYYY')} , {moment(note.Date).format("h:mma")}</p>
                    <p className="card-text">{moment(note.Date).fromNow()}</p>
                </div>
            </div>
        </div>
    )
}

export default NoteItem
